/* Importing a new font */
@font-face {
  font-family: 'ProductSans';
  src: local('ProductSans'), url(./assets/fonts/Product-Sans-Regular.ttf) format('truetype');
}

/* Scrollbar styling */
::-webkit-scrollbar {
  width: 6px;
}

::-webkit-scrollbar-track {
  background: #4e4a4a; 
}

::-webkit-scrollbar-thumb {
  background: rgb(36, 33, 33); 
}

/* All Animation here */
@include media-breakpoint-only(xs) { 
  .ScreenContainer {
    width: 100% !important;
  }
}
@include media-breakpoint-only(sm) {

}
@include media-breakpoint-only(md) {

}
@include media-breakpoint-only(lg) {

}
@include media-breakpoint-only(xl) {

}

@media (max-width: 768px) {

  .experience-element-holder {
    padding: 0 0 0 10px !important;
  }

  .experience-info-holder {
    width: calc(100vw - 100px);
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}

/* Small devices (landscape phones, 576px and up) */
@media (max-width: 991px) {
  .ScreenContainer {
    width: 100%;
  }

  .about-text-1 {
    font-size: 40px;
  }

  .about-text-2 {
    font-size: 18px !important;
  }

  .NavContainer {
    width: 0px;
    margin-top: -70px;
    opacity: 0.98;
  }

  .NavbarOpenAnimation {
    transition: 0.5s;
    -webkit-transition: 0.5s;
    -moz-transition: 0.5s;
    -o-transition: 0.5s;
    -ms-transition: 0.5s;
    width: 100vw;
  }

  .NavbarCloseAnimation {
    transition: 0.5s;
    -webkit-transition: 0.5s;
    -moz-transition: 0.5s;
    -o-transition: 0.5s;
    -ms-transition: 0.5s;
    width: 0px;
  }
  
  .hamburger-icon {
    color: white;
    font-size: 30px;
    padding: 12px;
    margin-left: 12px;
  }

  .budding-programmar-text {
    font-size: 20px;
    margin-top: 30px;
  }

  .about-text-1 {
    padding: 10px 14px;
  }

  .about-text-2 {
    padding: 30px 10px;
  }

  .nav-active {
    border-radius: 0 15px 15px 0;
  }

  .line {
    left: 50px;
  }
}


/* Large devices (desktops, 992px and up) */
@media (min-width: 992px) {
  .ScreenContainer {
    margin-left: 300px;
    width: calc(100vw - 300px)
  }

  .about-text-1 {
    font-size: 4vw;
  }

  .NavContainer {
    width: 300px;
  }

  .budding-programmar-text {
    font-size: 35px;
    margin-top: 60px;
  }

  .about-text-1 {
    padding: 70px 25px;
  }

  .about-text-2 {
    padding: 70px 20px;
  }

  .line {
    left: 350px;
  }
}

