.App {
  display: flex;
}
/* Nav Style Beginning Here */

.navbar-close {
  font-size: 25px;
  padding: 12px;
  margin-right: 12px;
  color: rgb(230, 88, 88);
  float: right;
}


.NavContainer {
  background-color: #ffe9e9f2;
  min-height: 100vh;
  height: 100%;
  overflow-y: scroll;
  position: fixed;
  z-index: 3;
}

.Nav {
  display: flex;
  flex-direction: column;
}

.Nav > a {
  text-decoration: none;
}

.NavbarLink {
  color: #000;
  text-decoration: none;
  font-family: 'Pragati Narrow', sans-serif;
  font-weight: 100;
  font-size: 25px;
  padding: 8px 12px;
  display: flex;
  align-items: center;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.Nav-icon {
  margin-left: 12px;
  margin-right: 12px;
}

.pp {
  width: 170px;
  height: 170px;
  border-radius: 50%;
  margin-bottom: 20px;
}

.pp-container {
  height: 360px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.pp-name {
  font-size: 25px;
  font-family: ProductSans;
  color: #000;
}

.pp-desc {
  font-family: 'Pragati Narrow', sans-serif;
  color: #464646;
  font-size: 18px;
}

.logo {
  display: flex;
  justify-content: center;
  align-items: flex-end;
}

.logo > img {
  width: 200px;
}

/* Nav Style ended here */

.ScreenContainer {
  min-height: 100vh;
  padding-top: 60px;
}

.ScreenPage {
  padding: 0 15px;
}

.float-left {
  float: left;
}

.float-right {
  float: right;
}

.nav-active {
  background-color: #b9b7b7;
  border-left-color: red;
  border-left-style: solid;
  border-left-width: 6px;
}

.nav-inactive {
  transition: 0.5s;
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -o-transition: 0.5s;
  -ms-transition: 0.5s;
  padding-left: 0px;
}

.about-text-1 {
  color: #000;
  font-family: 'Rubik', sans-serif;
  font-weight: bold;
  margin-bottom: 28px;
}

.about-text-2 {
  color: #000;
  font-family: 'Rubik', sans-serif;
  font-size: 20px;
}

/* Experience Page started here */

.circle-image {
  width: 80px;
  height: 80px;
  background-color: white;
  border-radius: 50%;
  z-index: 2;
}

.circle-image-holder {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.line {
  height: 100px;
  width: 6px;
  align-items: center;
  border-left: 6px solid #000;
}

.experience-element-holder {
  padding: 0px 0px 0px 55px;
  display: flex;
  flex-direction: row;
}

.experience-info-holder {
  padding-left: 15px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.experience-position {
  color: #000;
  font-size: 24px;
  font-family: 'Rubik', sans-serif;
}

.experience-other-info {
  color: rgb(125, 123, 123);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.experience-side-image {
  position: fixed;
  width: 30vw;
  right: 45px;
  top: 25vh;
}

/* Experience Page ended here */

/* Skills Page Starts here */

.skills-container {
  padding: 0 25px;
}

.heading {
  color: #bd2130;
  text-align: center;
}

.below-heading {
  color: rgb(125, 123, 123);
  font-family: 'Courier New', Courier, monospace;
  font-size: 18px;
  text-align: center;
  margin-bottom: 30px;
}

/* Skills Page Ends here */

/* Portfolio Page Starts Here */

.project-categories {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.project-categories > div {
  margin: 0 15px;
  font: 20px sans-serif;
  color: #000;
  cursor: pointer;
}

.project-categories > div:hover {
  color: rgb(202, 101, 228);
}

.project-cat-active {
  color: rgb(202, 101, 228) !important;
}

.projects-image {
  height: 150px;
  width: 90% !important;
  margin-top: 10px;
}

.card-centered {
  justify-content: center;
  align-items: center;
  background-color: #ffd6e3;
  min-height: 350px;
}

.project-display-true {
  transition: 0.5s;
    -webkit-transition: 0.5s;
    -moz-transition: 0.5s;
    -o-transition: 0.5s;
    -ms-transition: 0.5s;
    /* display: block; */
    opacity: 1;
}

.project-display-false {
    transition: 0.5s;
    -webkit-transition: 0.5s;
    -moz-transition: 0.5s;
    -o-transition: 0.5s;
    -ms-transition: 0.5s;
    /* display: none; */
    opacity: 0.2;
}

/* keyframes for projects animation */
@-webkit-keyframes fadeInFromNone {
  0% {
    display: none;
    opacity: 0;
  }
  1% {
    display: block;
    opacity: 0;
  }
  100% {
    display: block;
    opacity: 1;
  }
}
@-moz-keyframes fadeInFromNone {
  0% {
    display: none;
    opacity: 0;
  }
  1% {
    display: block;
    opacity: 0;
  }
  100% {
    display: block;
    opacity: 1;
  }
}
@-o-keyframes fadeInFromNone {
  0% {
    display: none;
    opacity: 0;
  }
  1% {
    display: block;
    opacity: 0;
  }
  100% {
    display: block;
    opacity: 1;
  }
}
@keyframes fadeInFromNone {
  0% {
    display: none;
    opacity: 0;
  }
  1% {
    display: block;
    opacity: 0;
  }
  100% {
    display: block;
    opacity: 1;
  }
}

@-webkit-keyframes fadeOut {
  0% {
    display: block;
    opacity: 1;
  }
  99% {
    display: block;
    opacity: 0;
  }
  100% {
    display: none;
  }
}
@-moz-keyframes fadeOut {
  0% {
    display: block;
    opacity: 1;
  }
  99% {
    display: block;
    opacity: 0;
  }
  100% {
    display: none;
  }
}
@-o-keyframes fadeOut {
  0% {
    display: block;
    opacity: 1;
  }
  99% {
    display: block;
    opacity: 0;
  }
  100% {
    display: none;
  }
}
@keyframes fadeOut {
  0% {
    display: block;
    opacity: 1;
  }
  99% {
    display: block;
    opacity: 0;
  }
  100% {
    display: none;
  }
}
/* keyframes ends here */

.text-align-center {
  text-align: center;
}
